* {
    box-sizing: border-box;
  }
  
body {
    margin: 0;
    font-family:  sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: radial-gradient(#303b37, #1a1f1d);
    color: #e1eeeb;
    min-height: 100vh;
  }

header{
    text-align: center;
    color: #83e6c0;
    

}


header img {
    width: 10rem;
    height: 10rem;
    object-fit: contain;
    background-color: transparent;
  }
#user-input {
    display: flex;
    justify-content: space-evenly;
    gap: 1.5rem;
    padding: 1rem;
    max-width: 60rem;
    
    margin: 2rem auto;
    border-radius: 4px;
    background: linear-gradient(180deg, #307e6c, #2b996d);
  }
#user-input label {
    display: block;
    margin-bottom: 0.25rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 0.75rem;
    font-weight: normal;
    text-transform: uppercase;
  }
  
#user-input input {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #76c0ae;
    border-radius: 0.25rem;
    background-color: transparent;
    color: #c2e9e0;
    font-size: 1rem;
  }
#result {
    max-width: 75rem;
    margin: 2rem auto;
    padding: 1rem;
    table-layout: fixed;
    border-spacing: 1rem;
    text-align: right;
  }
  
#result thead {
    font-size: 0.7rem;
    color: #83e6c0;
  }
  
#result tbody {
    font-family:  sans-serif;
    font-size: 0.85rem;
    color: #c2e9e0;
  }